import React from "react";
import verseService from '../services/verse'
import { useState } from "react";

const moodList = [
  "motivated", "joyful", "grateful", "peaceful", "hopeful",
  "inspired", "content", "optimistic", "energetic", "blessed"
];

const Home = () => {
  const [verse, setVerse] = useState(null)
  
  const handleClick = async (mood) => {
    try {
      const result = await verseService.fetchRandomVerse(mood)
      setVerse(result)
    } catch (e) {
      console.log(e)
    } 
  }

  return (
    <div>
      <section className="logo">
        <h2>Bible Quote Generator</h2>
      </section>
      <section>   
        <h1>Discover divine wisdom tailored to your mood with our innovative Bible Quote Generator</h1>
        <p>How are you feeling right now?</p>
      </section>      
      <section>
        { moodList.map(mood => <button key={mood} onClick={() => handleClick(mood)}>{mood}</button>) }
      </section>  
      <section>
        {verse ? verse.text : null}
      </section>
    </div>
  )
}

export default Home